@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,900;1,400;1,600;1,900&family=Notable&display=swap);
body{margin:0;font-family:'Montserrat', sans-serif}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.App{text-align:center}

.home__container{width:100%;height:100vh;background:url(/Contacts/static/media/cover-image.d6eacf85.jpg) no-repeat center 90%}.home__container h1,.home__container h2{color:#C7E2F7;font-family:'Montserrat', sans-serif}.home__container h1{font-weight:900;font-style:italic;font-size:70px}.home__container h2{font-size:30px;font-weight:300}.home__header{height:100px;display:flex;justify-content:space-between;padding:0 50px;align-items:center}.home__logo{font-size:40px;color:#C7E2F7}.home__header button{all:unset;border:2px solid #C7E2F7;border-radius:10px;padding:10px;color:#C7E2F7;transition:all 0.2s;cursor:pointer}.home__titles{margin-top:200px}.home__links{display:flex;justify-content:space-evenly;align-items:center;width:250px}.home__links>*{all:unset;cursor:pointer;color:#C7E2F7}@media (max-height: 900px){.home__container{background:url(/Contacts/static/media/cover-image.d6eacf85.jpg) no-repeat center 65%}.home__titles{margin-top:100px}}

.login__container a{all:unset;cursor:pointer;color:#C7E2F7}.login__container p{all:unset;cursor:pointer;color:#C7E2F7;margin-bottom:20px}.loader{margin-top:100px}.login__container main{display:flex;flex-direction:column;align-items:center;width:100%;min-height:calc(100vh - 80px);margin-top:80px;background:black}.login__form{display:flex;flex-direction:column;justify-content:flex-end;margin:0 auto;width:400px;height:200px;margin-top:150px}.login__form input{border:1px solid #004890;border-radius:10px;padding:10px;outline:none}.login__form button{all:unset;border:2px solid #004890;border-radius:10px;padding:10px;color:#004890;font-weight:700;transition:all .2s;cursor:pointer}.login__form button:hover{background-color:#004890;color:#C7E2F7}.login__form>*{margin-bottom:25px}@media (max-height: 900px){.login__container main{min-height:calc(100vh - 120px) !important;margin-top:60px}}

.header{position:fixed;top:0;display:flex;justify-content:space-evenly;align-items:center;width:100%;height:80px;background:url(/Contacts/static/media/cover-image.d6eacf85.jpg) no-repeat center 68%}@media (max-height: 900px){.header{background:url(/Contacts/static/media/cover-image.d6eacf85.jpg) no-repeat center 68.5%;height:60px}}

.footer{position:fixed;bottom:0;display:flex;justify-content:space-evenly;align-items:center;width:100%;height:100px;color:#C7E2F7;background:url(/Contacts/static/media/cover-image.d6eacf85.jpg) no-repeat center 94.5%}@media (max-height: 900px){.footer{background:url(/Contacts/static/media/cover-image.d6eacf85.jpg) no-repeat center 93%;height:60px}}

.signup__container a{all:unset;cursor:pointer;color:#C7E2F7}.signup__container p{all:unset;cursor:pointer;color:#C7E2F7;margin-bottom:20px}.loader{margin-top:100px}.signup__container main{display:flex;flex-direction:column;align-items:center;width:100%;min-height:calc(100vh - 80px);margin-top:80px;background:black}.signup__form{display:flex;flex-direction:column;justify-content:flex-end;margin:0 auto;width:400px;height:200px;margin-top:150px}.signup__form input{border:1px solid #004890;border-radius:10px;padding:10px;outline:none}.signup__form button{all:unset;border:2px solid #004890;border-radius:10px;padding:10px;color:#004890;font-weight:700;transition:all .2s;cursor:pointer}.signup__form button:hover{background-color:#004890;color:#C7E2F7}.signup__form>*{margin-bottom:25px}@media (max-height: 900px){.signup__container main{min-height:calc(100vh - 120px) !important;margin-top:60px}}

.dashboard__container{margin:0 auto;width:100%;min-height:100vh;display:flex;flex-direction:column;align-items:center}.dashboard__btn-logout,.dashboard__link-contacts{all:unset;cursor:pointer;color:#C7E2F7}.dashboard__container p{color:#C7E2F7}.dashboard__top>*{margin:0}

.top{display:flex;justify-content:space-around;align-items:center;width:100%;height:30px;background-color:black;font-size:12px;z-index:4}

nav{display:flex;justify-content:space-evenly;align-items:center;width:100%;height:50px;background:url(/Contacts/static/media/cover-image.d6eacf85.jpg) no-repeat center 68.5%}.nav__toggle{display:none !important}.nav__container{width:100%;height:auto}.nav__link:not(:first-child){margin-left:100px}.nav__link{all:unset;cursor:pointer;color:#C7E2F7}@media (max-height: 900px){nav{height:60px}}@media (max-width: 600px){nav{display:flex;flex-direction:column;align-items:flex-end;z-index:3;transform-style:preserve-3d}.nav__toggle{display:block !important;margin-right:20px;font-size:30px;color:#C7E2F7;background:transparent;border:none;outline:none;cursor:pointer}.nav__links{display:flex;flex-direction:column;place-items:center !important;position:absolute;background:#0e1111;width:100%;transform:translateZ(-10px) translateY(-150px);transition:all 0.5s linear}.nav__link{display:flex;justify-content:center;align-items:center;width:100%;height:30px;margin:0 !important;border-bottom:1px solid #C7E2F7}.show__links{transform:translateY(0)}}

.form__container input{border:1px solid #004890;border-radius:10px;padding:10px;outline:none}.form__info{display:flex;flex-direction:column;margin:0 auto;width:300px;margin-top:80px}.form__info>*{margin-bottom:20px}.form__info button{all:unset;border:2px solid #004890;border-radius:10px;padding:10px;color:#004890;font-weight:700;transition:all .2s;cursor:pointer}.form__info button:hover{background-color:#004890;color:#C7E2F7}.form__info button:disabled{background-color:lightgray;border-color:lightgray;color:grey;cursor:not-allowed;transition:none}.form__button-enter{margin-bottom:20px !important}.form__date{display:flex;flex-direction:column;align-items:center}.form__date label{font-size:12px;margin-bottom:5px}.form__date input{border:1px solid #004890;border-radius:10px;padding:10px;outline:none}.form__message{margin-top:10px;padding:3px 10px;border-radius:10px;color:#C7E2F7;font-weight:400}.success{background:#004890}.error{background:crimson}@media (max-height: 900px){.form__container input{padding:7px 10px !important}.form__info{margin-top:30px}.form__info button{padding:7px 10px !important}}

.subform__contacts{display:flex;flex-direction:column;margin:0 auto;width:300px;margin-top:80px;margin-top:0 !important}.subform__contacts>*{margin-bottom:20px}.subform__mobile,.subform__phone{display:flex;flex-direction:column}:is(.subform__mobile,.subform__phone) label{font-size:12px;margin-bottom:5px;display:flex;align-items:flex-start}.subform__email{margin-top:15px}@media (max-height: 900px){.subform__contacts>*{margin-bottom:15px}}

.main{display:flex;flex-direction:column;align-items:center;width:100%;min-height:calc(100vh - 80px)}

.contacts__container{margin:0 auto;width:100%;min-height:100vh;display:flex;flex-direction:column;align-items:center}.contacts__container input{border:1px solid black;border-radius:5px;height:22px}.contacts__link{all:unset;cursor:pointer;color:#C7E2F7}.contacts__table,tr,td{border:1px solid #d3d3d3;border-collapse:collapse;padding:10px;margin-top:80px}.contacts__table a{text-decoration:none}.contacts__sort{display:flex;align-items:center}.contacts__sort p{color:#C7E2F7;margin-right:5px}@media (max-height: 900px){.contacts__table,th,tr,td{padding:5px !important;font-size:14px !important}.contacts__container input{padding:5px 10px !important}}

.pagination__container{display:flex;flex-direction:column;margin:20px auto;width:40%;display:flex;place-items:center}.pagination{display:flex;place-items:center}.pagination__num{margin:0 10px;list-style:none;cursor:pointer;display:flex;place-content:center;place-items:center;width:30px;height:30px;border:1px solid #004890;border-radius:30px}.pagination__num a{text-decoration:none}.pagination__control{cursor:pointer}.pagination__sort{display:flex;place-items:center}.pagination__sort p{margin-right:5px}@media (max-height: 900px){.pagination__num{width:25px;height:25px;border-radius:25px}}

.details__container{margin:0 auto;width:100%;min-height:100vh;display:flex;flex-direction:column;align-items:center}.details__btn{all:unset;border:2px solid #004890;border-radius:10px;padding:10px;color:#004890;font-weight:700;transition:all .2s;cursor:pointer;margin-top:80px}.details__btn:hover{background-color:#004890;color:#C7E2F7}.details__table{margin-top:30px}.details__table,th,tr{border:1px solid #d3d3d3;border-collapse:collapse;padding:10px}@media (max-height: 900px){.details__container button{padding:7px 10px !important}}

.update__container{margin:0 auto;width:100%;min-height:100vh;display:flex;flex-direction:column;align-items:center}.update__form{display:flex;flex-direction:column;margin:0 auto;width:300px;margin-top:80px}.update__form input{border:1px solid #004890;border-radius:10px;padding:10px;outline:none}.update__form button{all:unset;border:2px solid #004890;border-radius:10px;padding:10px;color:#004890;font-weight:700;transition:all .2s;cursor:pointer}.update__form button:hover{background-color:#004890;color:#C7E2F7}.update__form button:disabled{background-color:lightgray;border-color:lightgray;color:grey;cursor:not-allowed;transition:none}.update__form>*{margin-bottom:20px}.update__message{margin-bottom:15px;padding:3px 10px;border-radius:10px;color:#C7E2F7;font-weight:400}.success{background:#004890}.error{background:crimson}@media (max-height: 900px){.update__form input{padding:7px 10px !important}.update__form button{padding:7px 10px !important}}

.favorites__container{margin:0 auto;width:100%;min-height:100vh;display:flex;flex-direction:column;align-items:center}.favorites__link{all:unset;cursor:pointer;color:#C7E2F7}.favorites__table,tr,td{border:1px solid #d3d3d3;border-collapse:collapse;padding:10px;margin-top:80px}.favorites__table a{text-decoration:none}

