@import '../../styles/mixins.scss';

.home__container {
  width: 100%;
  height: 100vh;
  background: url('../../assets/cover-image.jpg') no-repeat center 90%;
}

.home__container h1,
.home__container h2 {
  color: $color-font;
  font-family: 'Montserrat', sans-serif;
}

.home__container h1 {
  font-weight: 900;
  font-style: italic;
  font-size: 70px;
}

.home__container h2 {
  font-size: 30px;
  font-weight: 300;
}

.home__header {
  height: 100px;
  display: flex;
  justify-content: space-between;
  padding: 0 50px;
  align-items: center;
}

.home__logo {
  font-size: 40px;
  color: $color-font;
}

.home__header button {
  all: unset;
  border: 2px solid $color-font;
  border-radius: 10px;
  padding: 10px;
  color: $color-font;
  transition: all 0.2s;
  cursor: pointer;
}

.home__titles {
  margin-top: 200px;
}

.home__links {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 250px;
}

.home__links > * {
  @include link;
}

@media (max-height: 900px) {
  .home__container {
    background: url('../../assets/cover-image.jpg') no-repeat center 65%;
  }

  .home__titles {
    margin-top: 100px;
  }
}
